import { WalletAdapterNetwork } from "@solana/wallet-adapter-base";
import { ConnectionProvider, WalletProvider, useWallet } from "@solana/wallet-adapter-react";
import { WalletModalProvider, WalletMultiButton } from "@solana/wallet-adapter-react-ui";
import {
  GlowWalletAdapter,
  PhantomWalletAdapter,
  SlopeWalletAdapter,
  SolflareWalletAdapter,
  SolletExtensionWalletAdapter,
  SolletWalletAdapter,
  TorusWalletAdapter,
  UnsafeBurnerWalletAdapter
} from "@solana/wallet-adapter-wallets";
import { clusterApiUrl } from "@solana/web3.js";
import React, { FC, ReactNode, useEffect, useMemo, useState } from "react";
import axios from "axios";
import {useSelector} from "react-redux";

require("./WalletAdapter.css");
require("@solana/wallet-adapter-react-ui/styles.css");

const App: FC = () => {
  return (
    <Context>
      <Content />
    </Context>
  );
};
export default App;

const Context: FC<{ children: ReactNode }> = ({ children }) => {
  // The network can be set to 'devnet', 'testnet', or 'mainnet-beta'.
  const network = WalletAdapterNetwork.Devnet;

  // You can also provide a custom RPC endpoint.
  const endpoint = useMemo(() => clusterApiUrl(network), [network]);

  const wallets = useMemo(
    () => [
      new PhantomWalletAdapter(),
      new UnsafeBurnerWalletAdapter(),
      new GlowWalletAdapter(),
      new SlopeWalletAdapter(),
      new SolflareWalletAdapter(),
      new TorusWalletAdapter(),
      new SolletExtensionWalletAdapter(),
      new SolletWalletAdapter()
    ],
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [network]
  );

  return (
    <ConnectionProvider endpoint={endpoint}>
      <WalletProvider wallets={wallets} autoConnect>
        <WalletModalProvider>{children}</WalletModalProvider>
      </WalletProvider>
    </ConnectionProvider>
  );
};

const Content: FC = () => {
  const { publicKey, disconnect, connected } = useWallet();
  const [walletSelected, setWalletSelected] = useState(true);
  const [walletAddress, setWalletAddress] = useState("Empty")
  const [success,setSuccess] = useState(false);
  const [error,setError] = useState(false);
  const [successText, setSuccessText] = useState("");
  const [walletChecked, setWalletChecked] = useState(false);

  const base58 = useMemo(() => publicKey?.toBase58(), [publicKey]);
  const removeWallet = async () => {
    try{
      await axios.put("/users/wallet/delete", { wallet: "wallet" }, {
        headers: {
          "Content-Type": "application/json"
        }
      });
      setSuccess(true);
      setTimeout(() => setSuccess(false), 3000)
    }catch(err){
      setError(true);
      setTimeout(() => setError(false), 3000)
    }
  };

  useEffect(() => {
    if(connected === true && typeof base58 !== "undefined" && !walletSelected && walletChecked){
      const addNewWallet = async () => {
        try{
          await axios.put("/users/wallet/add", { wallet: base58 }, {
            headers: {
              "Content-Type": "application/json"
            }
          });
          setSuccess(true);
          setSuccessText("New wallet added")
          setTimeout(() => setSuccess(false), 3000)
        }catch(err){
          setError(true);
          setTimeout(() => setError(false), 3000)
        }
      };
      let selectedWalletAddress = typeof base58 !== "undefined" ? base58.toString() : "";
      setWalletSelected(true);
      selectedWalletAddress = `${selectedWalletAddress.substring(0, 6)}...${selectedWalletAddress.substring(selectedWalletAddress.length - 6)}`
      setWalletAddress(selectedWalletAddress);
      addNewWallet();
    }
  }, [connected]);

  useEffect(() => {
    axios("/users", {withCredentials: true})
      .then((response) => {
        setWalletAddress(response.data.data.walletAddress);
        if(response.data.data.walletAddress === "Empty"){
          setWalletSelected(false)
          setWalletChecked(true);
        }else{
          setWalletSelected(true);
          setWalletChecked(true);
        }

      })
      .catch(() => {
        setError(true);
        setTimeout(() => {
          setError(false)
        }, 3000)
      })
  }, [])

  return (
    <>
      <div className="wallet-adapter-container">
        <WalletMultiButton />
      </div>
    </>
  );
};
